.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f5f5f5;
  font-family: Arial, sans-serif;
}

.item-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 2px solid #ccc;
  margin-top: 10px;
  border-radius: 10px;
  padding: 10px;
  background-color: #ffffff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.item-container:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}

.text-container {
  width: 80%;
  padding-right: 10px;
}

.text-container p {
  margin: 5px 0;
  font-size: 16px;
  color: #333;
}

.checkbox-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

input[type="checkbox"] {
  width: 30px;
  height: 30px;
  cursor: pointer;
  accent-color: #4caf50;
}
